import allPage from "@/mixin/allPage_MX";
export default {
	mixins: [allPage],
	components: {
		
	},
	beforeCreate() {

	},
	data() {
		return {
			options:null,
			currentIndex: null,
			taskDetail:{},
			journalList: [{
				'name': '上刊（2）',
				'time': '05.01-05.01'
			},{
				'name': '监播1',
				'time': '05.01-05.02'
			},{
				'name': '监播2',
				'time': '05.01-05.03'
			},{
				'name': '下刊',
				'time': '05.01-05.04'
			}],
			showList:['中国', '美国', '巴西', '日本'],
		}
	},
	computed: {
		userLogin() {
			return this.$store.state.userLogin.userLogin
		},
	},
	onShow: function() {
		
	},
	onLoad: function(options) {
		this.options = options;
		this.getData();
	},
	onReady: function() {
		
	},
	onHide: function(options) {
		
	},
	onReachBottom: function(options) {
		
	},

	onPullDownRefresh() {
		
	},
	mounted() {
		
	},
	watch: {
		
	},
	onPageScroll: function(e) {
		
	},
	methods: {
		getData(){
			let parms = ',"openid":"' + this.userLogin.user.openid + '"},"taskQuery":{"taskId":' + this.options.id + '}';
			this.$store.dispatch('details/getTaskDetail', {parms,
				callback: (res2) => {
					console.log(res2);
					if (res2.errorCode == 0) {
						let data = res2.taskResult.tasks[0];
						this.taskDetail = data;
					}else{
						uni.showToast({
							title: res2.errorMsg,
							icon: 'none',
							mask: true
						})
					}
				},
			})
		},
		// 图片预览
		previewImage(item, index){
			// if(item.status == 1){
				if(item.spotClassTypeName == '视频'){
					let id = 'myvideo'+index;
					let videoContext = wx.createVideoContext(id, this);
				　　	videoContext.requestFullScreen();//执行全屏方法
				}else{
					wx.previewImage({
					  current: item.fileUrl,
					})
				}
			// }
		},
		// 退出全屏
		endAction(){
			
		},
		tabChange(index){
			this.currentIndex = index;
		},
		goPhotoSubmission(cameraType){
			uni.navigateTo({
			    url: "/pages/photoSubmission/photoSubmission?cameraType=" + cameraType + "&implement=1"
			})
		}
	},
}
